.colored-toast {
  background-color: #2250c6 !important;
}

.colored-toast .swal2-title {
  color: white !important;
}

.colored-toast .swal2-close {
  color: white !important;
}

.colored-toast .swal2-html-container {
  color: white !important;
}

.Center-Container .is-Flexbox {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
     -moz-box-align: center;
     -ms-flex-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-box-pack: center;
     -moz-box-pack: center;
     -ms-flex-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.exp-card {
  height: 100px;
  overflow: hidden;
  position: relative;
  text-align: center;
  align-items: center;
  justify-content: center;
  width: 100px;
}

.exp-card:hover .exp-description {
  -webkit-transform: translateY(0);
  transform: translateY(0);
}

.exp-description {
  bottom: 0;
  color: var(--white);
  left: 0;
  padding: 0.25rem;
  position: absolute;
  right: 0;
  -webkit-transform: translateY(100%);
  transform: translateY(100%);
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}

.shadow-nimble {box-shadow: 4.0px 8.0px 8.0px rgba(0,0,0,0.38)}
.shadow-blue {box-shadow: rgba(7, 65, 210, 0.1) 0px 9px 30px}
.shadow-red {box-shadow: 5px 5px 0px 0px rgba(255,0,0)}
.shadow-faintred {box-shadow: 0 25px 50px -12px #ff333320}
.shadow-aesthetic {box-shadow: 0 3px 10px rgb(0,0,0,0.2)}
.shadow-rededge {box-shadow: 0 0 0 2px var(--r), 8px 8px 0 0 var(--rd2)}
.shadow-edge {box-shadow: 0 0 0 2px var(--b4), 8px 8px 0 0 var(--d7);}
.shadow-glow {box-shadow: rgba(209, 209, 209, 0.1) 0px 0px 16px}
.shadow-subtle {box-shadow: rgba(0, 0, 0, 0.2) 0px 60px 40px -7px}
.shadow-inside {box-shadow: inset -12px -8px 40px #46464620}


.read-more { display: none; }
p:truncated + .read-more { display: block; }
.tooltip {position: relative}
.tooltip:hover .tooltiptext {visibility: visible;}
.base-page-size {width: 100%; min-height: 100vh}
.backdrop {backdrop-filter: blur(.5rem)}
    
.bg-vid {
  position: fixed;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 0;
  -webkit-animation: video-fade 6s ease-in both;
  animation: video-fade 6s ease-in both;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 160px;
  color: var(--l1);
  text-align: center;
  border-radius: 1rem;
  font-size: 1rem;
  position: absolute;
  z-index: 10;
  top: 90%;
  margin-top: 1.25rem;
  background: var(--d9);
  padding: 0.5rem;
  line-height: 1.5rem;
}